import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-rbac-permissions/@core/permissions/permission.store';
import { DataStoreService } from './pics-rbac-permissions/@core/service/data-store.service';
import { RBACINFO } from './pics-rbac-permissions/@core/urls/rbac-url.config';

@Component({
  selector: 'rbac-permissions',
  template: `
      <permissions></permissions>
  `,
  styles: [
  ]
})
export class RbacPermissionsComponent implements OnInit {

  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public permissionEvent!: Observable<any>;
  @Input() public COMMONSERVICE:Observable<any>;
  @Input() public INPUTVALIDATIONMETHOD? : any;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.permissionEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
      this._storeservice.setData('HTTPSERVICE',val.httpService);
      this._storeservice.setData('INPUTVALIDATIONMETHOD', this.INPUTVALIDATIONMETHOD);
    })
  }


}
